"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";
import { useAuth } from "@/context/Authcontext";


export default function StartPartyPage() {
  const [meetupPoint, setMeetupPoint] = useState("");
  const [dropOff, setDropOff] = useState("");
  const [partySize, setPartySize] = useState(1);
  const [expiresIn, setExpiresIn] = useState(60); // in minutes
  const [isFriendsOnly, setIsFriendsOnly] = useState(false);
  const [isGenderOnly, setIsGenderOnly] = useState(false);
  const [selectedRides, setSelectedRides] = useState<string[]>([]);

  const { user } = useAuth();
  const userId = user?.id;
  const router = useRouter();

  const handleStartParty = async () => {
    if (!meetupPoint || !dropOff || partySize <= 0) {
      alert("Please fill out the meetup point, drop-off, and set a party size.");
      return;
    }

    if (!userId) {
      alert("You must be logged in to host a party.");
      return;
    }

    // Required fields: start_time, end_time, seats_available
    const startTime = new Date();
    const endTime = new Date(startTime.getTime() + 60 * 60 * 1000); // +1hr

    const { error } = await supabase.from("parties").insert([
      {
        host_id: userId,
        meetup_point: meetupPoint,
        drop_off: dropOff,
        party_size: partySize,
        seats_available: partySize, // initially all seats available
        start_time: startTime.toISOString(),
        end_time: endTime.toISOString(),
        expires_in: `${expiresIn} minutes`, // TEXT in your schema
        friends_only: isFriendsOnly,
        gender_only: isGenderOnly,
        is_active: true,
        ride_options: selectedRides.length > 0 ? selectedRides.join(",") : null,
      },
    ]);

    if (error) {
      console.error("❌ Error creating party:", error);
      alert(error.message);
    } else {
      alert("✅ Party created!");
      router.push("/dashboard");
    }
  };

  return (
    <div className="max-w-lg mx-auto p-6">
      <h1 className="text-2xl font-bold mb-4">Start a Party</h1>

      <input
        type="text"
        placeholder="Meetup Point"
        value={meetupPoint}
        onChange={(e) => setMeetupPoint(e.target.value)}
        className="w-full p-2 mb-3 border rounded"
      />

      <input
        type="text"
        placeholder="Drop-off Point"
        value={dropOff}
        onChange={(e) => setDropOff(e.target.value)}
        className="w-full p-2 mb-3 border rounded"
      />

      <input
        type="number"
        placeholder="Party Size"
        min={1}
        max={8}
        value={partySize}
        onChange={(e) => setPartySize(parseInt(e.target.value))}
        className="w-full p-2 mb-3 border rounded"
      />

      <input
        type="number"
        placeholder="Expires In (minutes)"
        value={expiresIn}
        onChange={(e) => setExpiresIn(parseInt(e.target.value))}
        className="w-full p-2 mb-3 border rounded"
      />

      <div className="flex items-center gap-2 mb-3">
        <input
          type="checkbox"
          checked={isFriendsOnly}
          onChange={(e) => setIsFriendsOnly(e.target.checked)}
        />
        <label>Friends Only</label>
      </div>

      <div className="flex items-center gap-2 mb-3">
        <input
          type="checkbox"
          checked={isGenderOnly}
          onChange={(e) => setIsGenderOnly(e.target.checked)}
        />
        <label>Gender Only</label>
      </div>

      <button
        onClick={handleStartParty}
        className="w-full bg-blue-600 text-white py-2 rounded"
      >
        Start Party
      </button>
    </div>
  );
}
